import axios from 'axios'
import {urlApiBackend} from '../../variables/url/globalUrl'
import moment from "moment";



export const getPronostics = () => {
    return axios
      .get(`${urlApiBackend}/api/v1/predict/getpredict`, {
        headers: { "Content-Type": "application/json" }
      })
      .then(res => {
        return res.data;
      });
  };

 
  export const getPronosticsOld = () => {
    return axios
      .get(`${urlApiBackend}/api/v1/predict/getpredictold`, {
        headers: { "Content-Type": "application/json" }
      })
      .then(res => {
        let dataOld = [];
        let value = res.data;

        value.forEach((item) => {
           
            dataOld.push(item);
           
             
        })
        return dataOld;
      });
  };



export const addPronostic = (date,prediction,coast,type_prediction,author,championship,country,fixture) => {
    return axios
        .post(
            `${urlApiBackend}/api/v1/predict/addpredict`,
            {
                date:moment(date).format("YYYY-MM-DD"),
                prediction:prediction,
                coast:coast,
                type_prediction:type_prediction,
                author:author,
                iswin:"null",
                country :country,
                championship :championship,
                fixture:fixture,
            },
            {
                headers: { 'Content-Type': 'application/json', }
            }
        )
        .then(function(response) {
            console.log(response)
        })
}



export const deletePronostics = (fixture_id) => {
    return axios
      .delete(`${urlApiBackend}/api/v1/predict/deletepredict/${fixture_id}`, {
        headers: { "Content-Type": "application/json" }
      })
      .then(function(response) {
        console.log(response)
    })
    .catch(function(error) {
        console.log(error)
    })
  };


export const updatePronostic = (type_pronostic,country,championnat,match_date,match_time,team_home,team_away,odd,match_event, id) => {
    return axios
        .put(
            `${urlApiBackend}/api/pronostics/${id}`,
            {
                type_pronostic: type_pronostic,
                country:country,
                championnat:championnat,
                match_date:match_date,
                match_time:match_time,
                team_home:team_home,
                team_away:team_away,
                odd:odd,
                match_event:match_event
            },
            {
                headers: { 'Content-Type': 'application/json' }
            }
        )
        .then(function(response) {
            console.log(response)
        })
}
