import React from "react";
import classnames from "classnames";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { loginUser, setUserLoading } from "../actions/authActions";
import loader from "../assets/img/loader.gif";
import BackgroundImage from "../assets/img/backgroundLogin.png"
// reactstrap components
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  CardTitle,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Container,
  Row,
  Col
} from "reactstrap";


class LoginPage extends React.Component {

  constructor() {
    super();
    this.state = {
      email: "",
      password: "",
      isLoading:false
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.auth.isAuthenticated) {
      this.props.history.push("/admin/dashboard"); // push user to dashboard when they login
     
    }
 
    if (nextProps.auth.loading) {
      this.setState({
        isLoading: nextProps.auth.loading,
      });
    }
  }

  onChange = e => {
    this.setState({ [e.target.id]: e.target.value });
  };
  onSubmit = e => {
    e.preventDefault();
    const userData = {
      email: this.state.email,
      password: this.state.password
    };

    this.props.loginUser(userData); // since we handle the redirect within our component, we don't need to pass in this.props.history as a parameter
  };

  state = {
    squares1to6: "",
    squares7and8: ""
  };

  componentDidMount() {
    this.props.setUserLoading(false);
    document.body.classList.toggle("register-page");
    document.documentElement.addEventListener("mousemove", this.followCursor);
  }



  render() {
    const errors  = this.props.errors;
    return (
      <>
        <div className="wrapper" style={{backgroundImage: `url(${BackgroundImage})`,
  backgroundRepeat: 'no-repeat'}}>
          <div className="page-header">
            <div className="page-header-image" />
            <div className="content">
              <Container>
                <Row>
                  <Col className="offset-lg-0 offset-md-3" lg="5" md="6">
                    <Form className="form" noValidate onSubmit={this.onSubmit}>
                      <Card className="card-register">
                        <CardHeader >
                         
                          <CardTitle className="ml-3"  tag="h6" >Login</CardTitle>
                        </CardHeader>
                        <CardBody>
                        
                        {(typeof(errors) == "string" ? <div className="text-danger">{errors.error}</div> :<div className="text-danger">{errors.error}</div>)}      
                          <InputGroup
                            className={classnames({
                              "input-group-focus": this.state.fullNameFocus
                            })}
                           >

                          </InputGroup>
                          <InputGroup
                            className={classnames({
                              "input-group-focus": this.state.emailFocus
                            })}
                          >
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <i className="tim-icons icon-email-85" />
                              </InputGroupText>
                            </InputGroupAddon>
                            <Input
                              placeholder="Email"
                              style={{backgroundColor:'#96d3ec;'}}
                              color="muted"
                              onChange={this.onChange}
                              value={this.state.email}
                              id="email"
                              type="email"
                              onFocus={e => this.setState({ emailFocus: true })}
                              onBlur={e => this.setState({ emailFocus: false })}
                            />
                          </InputGroup>
                          <InputGroup
                            className={classnames({
                              "input-group-focus": this.state.passwordFocus
                            })}
                          >
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <i className="tim-icons icon-lock-circle" />
                              </InputGroupText>
                            </InputGroupAddon>
                            <Input
                              placeholder="Mot de Passe"
                              onChange={this.onChange}
                              value={this.state.password}
                              id="password"
                              type="password"
                              onFocus={e =>
                                this.setState({ passwordFocus: true })
                              }
                              onBlur={e =>
                                this.setState({ passwordFocus: false })
                              }
                            />
                          </InputGroup>
       

                        </CardBody>
                        <CardFooter>
                          <Button className="btn-round" type="submit" color="primary" size="lg">
                            Se Connecter 
                             {(this.state.isLoading ? <img src={loader} width="20" height="20" alt='loader' /> : "")}
                          </Button>

                        </CardFooter>
                      </Card>
                    </Form>

                  </Col>
                </Row>
             
              </Container>
            </div>
          </div>
        </div>
      </>
    );
  }
}

LoginPage.propTypes = {
  loginUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
};
const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors,
});

export default connect(
  mapStateToProps,
  { loginUser, setUserLoading }
)(LoginPage);


