import React from "react";
import { Snackbar } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
// reactstrap components
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  CardText,
  FormGroup,
  Form,
  Input,
  Row,
  Col
} from "reactstrap";
import {sendNotificationToAll} from "../Services/Notification/NotificationService";

class Notifications extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isToastr:false,
      title: "",
      body: "",
    };
        this.onChangeInputStates = this.onChangeInputStates.bind(this);

  }
  onSubmit = (e) => {
    e.preventDefault();
    sendNotificationToAll(
      this.state.title,
      this.state.body,
    ).then(() => {
      this.setState({
        isToastr: true,
      });
    });
  };

  handleCloseToastr = () => {
    return this.setState({
      isToastr: false,
    });
  };
 onChangeInputStates = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  render() {
    return (
      <>
        <div className="content">
          <Row>
            <Col md="8">
              <Card>
                <CardHeader>
                  <h4 className="title">Envoyer une notification de masse</h4>
                </CardHeader>
                <CardBody>
                  <Form onSubmit={this.onSubmit} >
                    {/* <Row>
                      <Col className="pr-md-1" md="5">
                        <FormGroup>
                          <label>Notification Predefini</label>
                          <Input type="select" name="select" id="exampleSelect" 
                          placeholder="Choisir Notification">
            <option>1</option>
            <option>Join VIP and WIN NOW</option>
            <option>3</option>
            <option>4</option>
            <option>5</option>
          </Input>
          
                        </FormGroup>
                      </Col> 
                    </Row> */}
                    <Row>
                      <Col className="pr-md-1" md="6">
                        <FormGroup>
                          <label>Titre</label>
                          <Input
                            
                            placeholder="Entrer Titre"
                            type="text"
                            name="title"
                             onChange={this.onChangeInputStates.bind(this)}
                          />
                        </FormGroup>
                      </Col>
                  
                    </Row>
           
                   
                    <Row>
                      <Col md="8">
                        <FormGroup>
                          <label>Description </label>
                          <Input
                            cols="80"
                            placeholder="Entrer Description"
                            rows="4"
                            type="textarea"
                            name="body"
                             onChange={this.onChangeInputStates.bind(this)}
                          />
                        </FormGroup>
                      </Col>
                    </Row>
                  </Form>
                </CardBody>
                <CardFooter>
                  <Button className="btn-fill" color="primary" type="submit" onClick={this.onSubmit}>
                    Envoyer
                  </Button>
                </CardFooter>
              </Card>
            </Col>
            <Col md="4">
              <Card className="card-user">
                <CardBody>
                  <CardText />
                  <div className="author">
                    <div className="block block-one" />
                    <div className="block block-two" />
                    <div className="block block-three" />
                    <div className="block block-four" />
                    <a href="#pablo" onClick={e => e.preventDefault()}>
                      <img
                        alt="..."
                        
                        src={require("../assets/img/notification_big_win.png")}
                      />
                      <h5 className="title">Exemple de Notification Sur Smartphone</h5>
                    </a>
                  </div>
                 
                </CardBody>
               
              </Card>
            </Col>

          </Row>
               <Snackbar
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={this.state.isToastr}
        autoHideDuration={2000}
        message="Notification envoye avec success"
        action={
          <React.Fragment>
          
            <IconButton size="small" aria-label="close" color="inherit" onClick={this.handleCloseToastr}>
              <CloseIcon fontSize="small" />
            </IconButton>
          </React.Fragment>
        }
      />
        </div>
      </>
    );
  }
}

export default Notifications;
