import axios from 'axios'
import {urlApiBackend} from '../../variables/url/globalUrl'

export const sendNotificationToAll = (title,body) => {
    return axios
        .post(
            `${urlApiBackend}/api/v1/notification/all`,
            {
                title :title,
                body:body,
            },
            {
                headers: { 'Content-Type': 'application/json', }
            }
        )
        .then(function(response) {
            console.log(response)
        })
}


