import axios from 'axios'
import {urlApiBackend} from '../../variables/url/globalUrl'

export const getCountryByDate = (date) => {
    return axios
        .get(`${urlApiBackend}/api/v1/fixtures/countrygames/${date}`, {
            headers: {   
            'Content-Type': 'application/json', 
        }
        })
        .then(res => {
            return res.data
        })
} 


export const getChampionshipByCountry = (date,country) => {
    return axios
        .get(`${urlApiBackend}/api/v1/fixtures/leaguescountry/${date}/${country}`, {
            headers: {   
            'Content-Type': 'application/json',
         }
        })
        .then(res => {
            return res.data
        })
} 


export const getMatchsByChampionship= (date,country,championship) => {
    return axios 
        .get(`${urlApiBackend}/api/v1/fixtures/matchsleague/${date}/${country}/${championship}`, {
            headers: {   
            'Content-Type': 'application/json',
         }
        })
        .then(res => {
            return res.data
        })
} 




