export const getCurrentDate = (separator='') => {

    let newDate = new Date()
    let date = newDate.getDate();
    let month = newDate.getMonth() + 1;
    let year = newDate.getFullYear();
    
    return `${year}${separator}${month<10?`0${month}`:`${month}`}${separator}${date}`
    }

    export const getTomorrowDate = (separator='') => {

        let newDate = new Date()
        let date = newDate.getDate() + 1;
        let month = newDate.getMonth() + 1;
        let year = newDate.getFullYear();
        
        return `${year}${separator}${month<10?`0${month}`:`${month}`}${separator}${date}`
        }

        export const get2DayAfterDate = (separator='') => {

            let newDate = new Date()
            let date = newDate.getDate() + 2;
            let month = newDate.getMonth() + 1;
            let year = newDate.getFullYear();
            
            return `${year}${separator}${month<10?`0${month}`:`${month}`}${separator}${date}`
            }

            export const getYesterdayDate = (separator='') => {

                let newDate = new Date()
                let date = newDate.getDate() - 1;
                let month = newDate.getMonth() + 1;
                let year = newDate.getFullYear();
                
                return `${year}${separator}${month<10?`0${month}`:`${month}`}${separator}${date}`
                }

                export const get1YesterdayDate = (separator='') => {

                    let newDate = new Date()
                    let date = newDate.getDate() - 2;
                    let month = newDate.getMonth() + 1;
                    let year = newDate.getFullYear();
                    
                    return `${year}${separator}${month<10?`0${month}`:`${month}`}${separator}${date}`
                    }

                    export const get2YesterdayDate = (separator='') => {

                        let newDate = new Date()
                        let date = newDate.getDate() - 3;
                        let month = newDate.getMonth() + 1;
                        let year = newDate.getFullYear();
                        
                        return `${year}${separator}${month<10?`0${month}`:`${month}`}${separator}${date}`
                        }